<template>
  <div>

    <page-loading v-if="loading"></page-loading>
    <no-data v-if="!loading && podcasts.length === 0" message="No podcast added"></no-data>


    <div v-if="!loading" class="row">

      <div class="col col-md-12 col-sm-12 col-12">
        <div class="row">
          <div :key="podcast.id" v-for="podcast in podcasts" class="col col-md-3 col-sm-12 col-12 mt-6">
            <vx-card>
              <img
                @click="goToPodcastItem(podcast.product, podcast.fid)"
                src="@/assets/images/custom/podcast-thumnail.png"
                alt="content-img"
                class="responsive rounded-lg cursor-pointer">


              <div class="text-center" style="height: 50px">
                <h5 @click="goToPodcastItem(podcast.product, podcast.fid)" class="cursor-pointer" style="direction: ltr">{{ podcast.item.title }}</h5>
              </div>
              <vs-divider></vs-divider>
              <div class="flex justify-between flex-wrap">
                <span>
                  <p class="text-grey">Subject</p>
                  <p class="text-xl mt-2">{{ podcast.item.subject.title }}</p>
                </span>

                <div class="flex">

                  <vs-button class="mt-4"
                             color="primary"
                             type="border"
                             icon-pack="feather"
                             icon="icon-bookmark"
                             :id="'add-bookmark-btn-' + podcast.item.id"
                             v-if="podcast.bookmark === null"
                             @click="addToBookmark(podcast, `add-bookmark-btn-` + podcast.item.id)"
                  >
                  </vs-button>

                  <vs-button class="mt-4"
                             color="primary"
                             icon-pack="feather"
                             icon="icon-bookmark"
                             :id="`remove-bookmark-btn-` + podcast.item.id"
                             v-if="podcast.bookmark !== null"
                             @click="removeFromBookmark(podcast, `remove-bookmark-btn-` + podcast.item.id)"
                  >
                  </vs-button>


                </div>

              </div>
            </vx-card>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../../../../http/axios";
import mixins from "../../../../mixins";
import vSelect from "vue-select";

const PAGE_SIZE = 25;
export default {
  name: "PodcastFavs",
  components: {'v-select': vSelect},
  mixins: [mixins],
  data() {
    return {
      podcasts: [],
      loading: true,
      subjects: [{id: null, label: "all subjects"}],
      selectedSubject: {id: null, label: "all subjects"},
    }
  },
  mounted() {
    this.getPodcasts();
  },
  methods: {
    getPodcasts() {

      this.loading = true;

      this.podcasts = [];
      let url = `/api/bookmarks?item_type=${this.PODCAST_TYPE}`
      axios.get(url, {
        headers: {
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        if (res.status === 200) {
          // this.podcasts = res.data;
          res.data.forEach(data => {
            data.item.bookmark = data.fid
            this.podcasts.push(data.item)
          });

        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
        this.loading = false;

      })
    },

    removeFromBookmark(podcast, btnId) {
      this.startLoading(btnId);
      axios.delete(`/api/bookmarks/${podcast.bookmark}`, {
        headers: {
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        this.stopLoading(btnId);
        if (res.status === 204) {
          this.getPodcasts();
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
      });
    },

    addToBookmark(podcast, btnId) {
      this.startLoading(btnId);
      axios.post(`/api/bookmarks/`, {
        item_fid: podcast.fid
      }, {
        headers: {
          Accept: "application/json",
          Authorization: this.getAccessToken()
        }
      }).then(res => {
        this.stopLoading(btnId);
        if (res.status === 201) {
          podcast.bookmark = res.data.fid;
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
      });
    },

    goToPodcastItem(productId, podcastFid) {
      this.$router.push({
        name: "podcast-item",
        params: {
          itemFid: podcastFid,
          productId: productId
        }
      })
    },

    filterBySubject() {
      // console.log(this.selectedSubject);
    }
  }
}
</script>

